.nav {
    position: sticky;
    top: 0;
    background: #2aa6de;
    z-index: 100;
    width: 100%;
    /* height:5rem */
}

.logo {
    width: 5rem;
}

nav a {
    text-decoration: none;
    color: #808080;
    font-size: larger;
}

.profile {
    height: 3rem;
    width: 3rem;
    border-radius: 1.5rem;
}

a:hover {
    text-decoration: none;
}
@media only screen and (max-width: 990px) {
    .profile {
      display: none;
    }
  }